import React, { Suspense } from "react";
import { isSocialEmbed } from "../../../../../actions/api";
import Image from "../../../Elements/Media/Image";
import Video from "../../../Elements/Media/Video";
import MediaIcon from "../../../Elements/MediaIcon/MediaIcon";
const Like = React.lazy(() => import('../../../Elements/Reactions/Like'));
const Media = ({ itemData, wallID, wall, ownerId, isVisible }) => {
  const isVideo = (itemData.type === 3 || itemData.type === 5) && isVisible ? true : false
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false


  return <div className="tb_g_r_media_wrap">
 {isSocialEmbed && wall.Personalization.interactive ? <Suspense fallback={<></>}><Like likeClass={`tb_g_r_like ${!wall.ThemeRule.socialAction ? 'tb_g_r_like_bottom':''}`} postId={itemData.id} wallID={wallID} UserDetail={wall.UserDetail} /></Suspense> : null}
    {isSocialEmbed ? <MediaIcon itemData={itemData} IconClass="tb_g_r_media_icon" show={true} hideVideo={true} />:null}
    {isVideo || autoPlay  ?
      <Video VideoClass="tb_g_r_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} isCover={true}  autoPlay={true} muted={true} />
      :
      <Image ImageClass="tb_g_r_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} />
    }
  </div>
}
export default Media