import React, { Suspense, Component, memo } from "react";
import { isMobile } from 'react-device-detect';
import { connect } from "react-redux";
import Masonry from 'react-masonry-component';
import Card from './Elements/Card'
//import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, FreeMode, Mousewheel } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/autoplay';
import 'swiper/css/free-mode';
import 'swiper/css/mousewheel';
import './scss/ReelTheme.scss'

import { getDataNextSteps } from '../../../../actions/themeActions'
import { mainClass, detectMobile } from '../../../../utils'
import { isSocialEmbed } from "../../../../actions/api";
const SwiperFalling = React.lazy(() => import("./Elements/SwiperFalling"));


class GReels extends Component {
    constructor(props) {
        super(props)
        this.inputRefs = {};
    }
    state = {
        activeID: -1,
        newPostdata: []
    }
    componentWillMount() {
        const { cardNumber, postData, completeDataObject, adjustWidth, wall, postSize, clickToShowPopUp, onClickToCTA, postSizeInPx, numberOfCoumn } = this.props;
        const { activeID } = this.state
        const column = wall.ThemeRule.numberOfCoumn;
        const newPostdata = Array.from({ length: column }, () => []);
        postData.forEach((post, index) => newPostdata[index % column].push(post));
        newPostdata.forEach(col => {
            while (col.length < 3) {
                const randomCol = newPostdata[Math.floor(Math.random() * column)];
                const randomItem = randomCol[Math.floor(Math.random() * randomCol.length)];
                col.push(randomItem);
            }
        });
        this.setState({ newPostdata })
    }
    scrollInterval = null
    componentDidMount = () => {
        this.scrollInterval = setInterval(() => this.onUpdateTimeInterVal(), 3000);
        const { heightUpdate } = this.props;
        setTimeout(() => heightUpdate(), 1000)
    };
    onUpdateTimeInterVal = () => {
        const { activeID } = this.state;
        const { cardNumber, wall } = this.props
        const column = wall.ThemeRule.numberOfCoumn;
        const nextID = (parseInt(activeID) + 1);
        this.setState({ activeID: parseInt(column) >= nextID ? nextID : 0 })
    };
    componentWillUnmount() {
        if (this.scrollInterval) clearInterval(this.scrollInterval);
    }




    render() {
        const { postData, completeDataObject, wall, clickToShowPopUp, hasMoreData, parentID, keyRender, onClickToCTA, postSizeInPx, adjustWidth, numberOfCoumn } = this.props;
        const { activeID, newPostdata } = this.state
        return <> {isSocialEmbed ? <Masonry
            id="pixel"
            className={`tb_g_r_post_container`}
            tabIndex='0'
            aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`}
            elementType={'div'}
            options={{
                transitionDuration: 0,
                percentPosition: true,
                itemSelector: '.tb_px_post_wrapper',
                horizontalOrder: true,
                resize: true,
            }}
            disableImagesLoaded={false}
            updateOnEachImageLoad={false}
            imagesLoadedOptions={{}}
            enableResizableChildren={true}>

            {(postData && postData.length > 0) && postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <Card ownerId={wall.Wall.owner} itemData={cardData} key={`sqr_crd_${index}_${item.id}`} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} wall={wall} />
            })}

        </Masonry>
            :
            <div className="tb_g_r_post_container">
                {
                    newPostdata.map((item, index) => {
                        return <Suspense fallback={<></>}>
                            <SwiperFalling
                                key={`exhi_${index}`}
                                pIndex={index}
                                itemIds={item}
                                activeID={activeID}
                                completeDataObject={completeDataObject}
                                wall={wall}
                                onClickToCTA={onClickToCTA}
                                postSizeInPx={postSizeInPx}
                                cardCount={newPostdata.length}
                                clickToShowPopUp={clickToShowPopUp}
                            />
                        </Suspense>
                    })
                }
            </div>}
        </>
    }
}

const mapStateToProps = state => {
    return {
        loaderData: state.loaderData,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
    }
}
export default memo(connect(mapStateToProps, mapDispatchToProps)(GReels));