import React, { PureComponent, Suspense } from "react";
import Media from "./Media";
import { themeImpressionTracking } from "../../../../../utils";
import { isTagShop, isTagEmbed, suiteTrack } from "../../../../../actions/api";
import Network from "../../../Elements/Network/Network";
import ShopButton from "../../../Elements/ShopButton/ShopButton";
const AuthorDetails = React.lazy(() => import("./AuthorDetails"));


export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.mediaHeight = React.createRef();
  }
  componentDidMount() {
    if (suiteTrack) themeImpressionTracking(this.mediaHeight)
  }

  render() {
    const { itemData, itemIndex, wallID, ownerId, wall, isVisible, clickToShowPopUp, ThemeRule, personalization,adjustWidth } = this.props;
    const isProduct = itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
    const isShopBtn = isTagShop && (isProduct) && !ThemeRule.hideContent ? true : false;
    const isShopIcon = isTagShop && (isProduct && itemData.hotspot === 0) ? true : false
    const isHotspot = isTagShop && itemData.hotspot && isProduct ? true : false


    return <div item-id={itemData.id} id={`tb-g_r-post-${itemData.id}`} className="tb_g_r_post_wrapper" ref={this.mediaHeight} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} style={{width: `${adjustWidth}%`, padding: wall.Personalization.padding / 2 }} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0}>
      <div className={`tb_g_r_post_in tb_icon_animate ${!isTagShop ? 'tb_g_r_post_in_animate' : ''}`} onClick={clickToShowPopUp(itemIndex, itemData)}>
        <div className="tb_g_r_post_media_wrapp">
          <Media itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} preload={itemIndex == 0 ? true : false} isVisible={isVisible} />
          <div className="tb_g_r_post_details">
            <Suspense fallback={<></>}><AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} /></Suspense>
          </div>
        </div>
      </div>
    </div>
  }
}
