import React from "react";
import { isTagEmbed, isSocialWall } from "../../../../actions/api";
import DefaultNetwork from "../Network/DefaultNetwork";
import "./scss/MediaIcon.scss"
const MediaIcon = (props) => {
       const { itemData, IconClass, isCenter, hideVideo, show } = props;

       const isMediaType = (itemData.type === 3 || itemData.type === 5 ) ? true : false
       const isYoutubePost = (itemData.network.id === 7) ? true : false
       const isMultiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
       const isAudio = itemData.isAudio === 1 ? true : false
       const isShopIcon = (itemData.ugc_products && itemData.ugc_products.length > 0) ? true : false;
       
       const youtube = <DefaultNetwork network={{ "id": 7, "name": "YouTube", "icon": "youtube" }} networkClass={'tb_youtube_ico'} />
       const multipleMedia = <div className={`tb_multiple_ico tb__icon tb-multiple`}><div></div></div>
       const audioIcon = <div className="tb_audio_ico tb__icon tb-audio"><div></div></div>
       const videoIcon = <div className={`tb__icon tb-${isSocialWall?'reel-fill tb_reel_ico':'video tb_video_ico'}`}><div></div></div>
       const playIcon = <div className="tb_play_ico tb__icon tb-play"><div></div></div>
       const shoppableIcon = <div className="tb_shop_ico tb__icon tb-bag"><div></div></div>
       const roundedVideoIcon = <div className="tb_center_video_ico tb__icon tb-play-circle"><div></div></div>

       return show ? <>{isCenter && (isYoutubePost || isMediaType) ? 
              <div className={`${IconClass} tb__media_ico_c`}>
                     {isMediaType && !isYoutubePost ? roundedVideoIcon : ''}
                     {isMediaType && isYoutubePost ? youtube : ''}
              </div>
              :null}
              
              {isShopIcon || isMultiImages || isMediaType ?
              <div className={`${IconClass} tb__media_ico_`}>
                     {isShopIcon ? shoppableIcon : ''}
                     {isMultiImages ? multipleMedia : ''}
                     {!isCenter && isMediaType && !isYoutubePost && !hideVideo ? isAudio ? audioIcon : isTagEmbed ? playIcon : videoIcon : ''}
              </div>: null}
              </> : null
}
export default MediaIcon