import React, { PureComponent } from "react";
import { isTagEmbed } from "../../../../actions/api";

const DefNetIcon = React.memo(({ network, networkClass }) => {
  const networkClasses = `${networkClass} tb__icon tb_ico_default`
  switch (network.id) {
    case 1:
      return <div className={`${networkClasses} tb-twitter`}></div>
    case 2:
      return <div className={`${networkClasses} tb-instagram-default`}><div></div></div>
    case 3:
      return <div className={`${networkClasses} tb-facebook`}><div></div></div>
    case 4:
      return <div className={`${networkClasses} tb-google-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div><div className="tb_colr_ico tb_ico_path3"><div></div></div><div className="tb_colr_ico tb_ico_path4"><div></div></div></div>
    case 5:
      return <div className={`${networkClasses} tb-pinterest`}><div></div></div>
    case 6:
      return <div className={`${networkClasses} tb-flickr-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div></div>
    case 7:
      return <div className={`${networkClasses} tb-youtube-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div></div>
    case 8:
      return <div className={`${networkClasses} tb-vimeo`}><div></div></div>
    case 10:
      return <div className={`${networkClasses} tb-linkedin`}><div></div></div>
    case 11:
      return <div className={`${networkClasses} tb-tumblr`}><div></div></div>
    case 12:
      return <div className={`${networkClasses} tb-rss`}><div></div></div>
    case 15:
      return <div className={`${networkClasses} tb-workplace`}><div></div></div>
    case 18:
      return <div className={`${networkClasses} tb-instagram-default`}><div></div></div>
    case 19:
      return <div className={`${networkClasses} tb-yelp`}><div></div></div>
    case 20:
      return <div className={`${networkClasses} tb-slack-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div><div className="tb_colr_ico tb_ico_path3"><div></div></div><div className="tb_colr_ico tb_ico_path4"><div></div></div><div className="tb_colr_ico tb_ico_path5"><div></div></div><div className="tb_colr_ico tb_ico_path6"><div></div></div><div className="tb_colr_ico tb_ico_path7"><div></div></div><div className="tb_colr_ico tb_ico_path8"><div></div></div></div>
    case 21:
      return <div className={`${networkClasses} tb-yammer`}><div></div></div>
    case 23:
      return <div className={`${networkClasses} tb-airbnb`}><div></div></div>
    case 25:
      return <div className={`${networkClasses} tb-soundcloud`}><div></div></div>
    case 26:
      return <div className={`${networkClasses} tb-giphy-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div><div className="tb_colr_ico tb_ico_path3"><div></div></div><div className="tb_colr_ico tb_ico_path4"><div></div></div><div className="tb_colr_ico tb_ico_path5"><div></div></div><div className="tb_colr_ico tb_ico_path6"><div></div></div><div className="tb_colr_ico tb_ico_path7"><div></div></div></div>
    case 28:
      return isTagEmbed ? <div className={`${networkClasses} tb-capterra-default`}><div className="tb_colr_ico tb_ico_path1"></div><div className="tb_colr_ico tb_ico_path2"></div><div className="tb_colr_ico tb_ico_path3"></div><div className="tb_colr_ico tb_ico_path4"></div></div> : <div className={`${networkClasses} tb-tiktok-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div><div className="tb_colr_ico tb_ico_path3"><div></div></div><div className="tb_colr_ico tb_ico_path4"><div></div></div><div className="tb_colr_ico tb_ico_path5"><div></div></div><div className="tb_colr_ico tb_ico_path6"><div></div></div><div className="tb_colr_ico tb_ico_path7"><div></div></div></div>
    case 29:
      return isTagEmbed ? <div className={`${networkClasses} tb-etsy`}><div></div></div> : <div className={`${networkClasses} tb-onsite-upload`}><div></div></div>
    case 30:
      return <div className={`${networkClasses} tb-vk`}><div></div></div>
    case 31:
      return <div className={`${networkClasses} tb-tiktok-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div><div className="tb_colr_ico tb_ico_path3"><div></div></div><div className="tb_colr_ico tb_ico_path4"><div></div></div><div className="tb_colr_ico tb_ico_path5"><div></div></div><div className="tb_colr_ico tb_ico_path6"><div></div></div><div className="tb_colr_ico tb_ico_path7"><div></div></div></div>
    case 32:
      return <div className={`${networkClasses} tb-vk`}><div></div></div>
    case 33:
      return <div className={`${networkClasses} tb-trustpilot-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div></div>
    case 34:
      return <div className={`${networkClasses} tb-amazon-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div></div>
    case 35:
      return <div className={`${networkClasses} tb-tripadvisor`}><div></div></div>
    case 36:
      return <div className={`${networkClasses} tb-tagembed-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div><div className="tb_colr_ico tb_ico_path3"><div></div></div></div>
    case 37:
      return <div className={`${networkClasses} tb-aliexpress-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div><div className="tb_colr_ico tb_ico_path3"><div></div></div><div className="tb_colr_ico tb_ico_path4"><div></div></div></div>
    case 38:
      return <div className={`${networkClasses} tb-booking-default`}><div className="tb_colr_ico tb_ico_path1"><div></div></div><div className="tb_colr_ico tb_ico_path2"><div></div></div><div className="tb_colr_ico tb_ico_path3"><div></div></div></div>
    default:
      return ''
  }
})
export default class DefaultNetwork extends PureComponent {
  render() {
    return <DefNetIcon {...this.props} />
  }
}
